import React from 'react';
import {LightVariant} from "./LightVariant"
import {SingleSelectDescription} from "./SingleSelectDescription"
import ReactDOM from 'react-dom';
import "@patternfly/react-core/dist/styles/base.css";
import './fonts.css';
import { Grid, GridItem } from '@patternfly/react-core';

function App(){

    const Basic = () => (
        <Grid hasGutter>
            <GridItem><LightVariant/></GridItem>
            <GridItem span={2}><SingleSelectDescription /></GridItem>
        </Grid>


    )
    const rootElement = document.getElementById("root");
    ReactDOM.render(<Basic />, rootElement);
}

export default App;