import "@patternfly/react-core/dist/styles/base.css";
import './fonts.css';

import React from 'react';
import { Select, SelectOption, SelectVariant } from '@patternfly/react-core';

export class SingleSelectDescription extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            items: [],
            DataisLoaded: false
        };


        this.options = [
            { value: 'Mr', disabled: false },
            { value: 'Miss', disabled: false },
            { value: 'Mrs', disabled: false },
            { value: 'Ms', disabled: false },
            { value: 'Dr', disabled: false },
            { value: 'Other', disabled: false }
        ];

        this.state = {
            isOpen: false,
            selected: null,
            isDisabled: false
        };

        this.onToggle = isOpen => {
            this.setState({
                isOpen
            });
        };

        this.onSelect = (event, selection, isPlaceholder) => {
            if (isPlaceholder) this.clearSelection();
            else {
                this.setState({
                    selected: selection,
                    isOpen: false
                });
                console.log('selected:', selection);
            }
        };

        this.clearSelection = () => {
            this.setState({
                selected: null,
                isOpen: false
            });
        };
    }

    componentDidMount() {
        fetch(
            "http://localhost:8080/api/particle")
            .then((res) => res.json())
            .then((json) => {
                this.setState({
                    items: json,
                    DataisLoaded: true
                });
            })
    }

    render() {
        const { DataisLoaded, items } = this.state;
        if (!DataisLoaded) return <div>
            <h1> Pleses wait some time.... </h1> </div> ;

        const { isOpen, selected, isDisabled, direction, isToggleIcon } = this.state;
        const titleId = 'select-descriptions-title';
        return (
            <div>
        <span id={titleId} hidden>
          Title
        </span>
                <Select
                    variant={SelectVariant.single}
                    placeholderText="Select an option"
                    aria-label="Select Input with descriptions"
                    onToggle={this.onToggle}
                    onSelect={this.onSelect}
                    selections={selected}
                    isOpen={isOpen}
                    aria-labelledby={titleId}
                    isDisabled={isDisabled}
                >
                    {items.map((item) => (
                        <SelectOption
                            key={item.id}
                            value={item.name}
                            description={item.stringValue1}
                        />
                    ))}
                </Select>
            </div>
        );
    }
}